:root {
  --background-accent: #1A1A1A;
  --background-primary: #fff;
  --background-secondary: rgba(26, 26, 26, 0.04);
  --background-primary-blur: rgba(26, 26, 26, 0.88);
  --background-input: rgba(255, 255, 255, 0.06);

  --text-primary: #1A1A1A;
  --text-primary-oncolor: #fff;
  --text-primary-oncolor-hover: rgba(255, 255, 255, 0.88);
  --text-primary-oncolor-inactive: rgba(255, 255, 255, 0.64);
  --text-secondary: rgba(26, 26, 26, 0.6);
  --text-secondary-oncolor: rgba(255, 255, 255, 0.64);
  --text-error: #FF2323;
  --text-disabled: rgba(26, 26, 26, 0.4);
  --text-and-icons-primary-on-color-disabled: rgba(255, 255, 255, 0.40);


}

body {
  margin: 0;
  padding: 0;
  width: 100vw;
  background-color: var(--background-primary);
  color: var(--text-primary);
  font-family: "TT Commons Pro Trial";
}

/* Typography classes */

.text {
  margin: 0;
  padding: 0;
  font-weight: 500;
  font-style: normal;
  text-decoration: none;
  text-align: left;
  font-family: "TT Commons Pro Trial";
}

