.text_color_primary {
  color: var(--text-primary);
}

.text_color_accent {
  color: var(--text-primary-oncolor);
}

.text_color_secondary_accent {
  color: var(--text-secondary-oncolor);
}

.text_color_secondary {
  color: var(--text-secondary);
}

.text_color_tertiary {
  color: var(--text-tertiary);
}

.text_color_error {
  color: var(--text-error)
}

.text_color_true {
